import {mapState} from 'vuex';
import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
import {turnPage} from '@/common/global.js'
import {ajaxGetGoodInfo, ajaxGetRecommendGoods} from '@/data/ajax.js'

export default {
	components: {
		uniIconfont,
		uniSwiperDot,
		uniBadge
	},
	computed: {
		selectResult () {
			return this.$store.getters.doneSelectResult;
		}
	},
	data () {
		return {
			current: 0,
			goodInfo: {},
			covers: [
				// {colorClass: 'uni-bg-blue', url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg',　content: '内容 C'}
			],
			dotsStyles: {
				backgroundColor: '#fff',
				border: '0',
				color: '#fff',
				selectedBackgroundColor: '#fff',
				selectedBorder: '0'
			},
			recommends: [],
			ggoption: {
				show: false,
				mainShow: false
			},
			specsIndex: 0
		};
	},
	onLoad (e) {
		// 获取商品详情
		let _this = this;
		ajaxGetGoodInfo(e, function (data) {
			let info = data.result;
			// banner
			_this.covers = [];
			info.cover.forEach( item => {
				_this.covers.push({url: item});
			});
			_this.goodInfo = info;
			// 默认规格设置
			_this.chooseSpecs(0);
			ajaxGetRecommendGoods('', function (data) {
				_this.recommends = data.result;
			});
		});
	},
	methods: {
		change (e) {
			this.current = e.detail.current;
		},
		clkCar () {
			turnPage('car');
		},
		clkAddCar () {
			// 规格选择
			
			
			// let _this = this;
			this.showGg();
			// this.$set(this.goodInfo, 'select', true);
			// this.$store.commit('addGood', [this.goodInfo, (data) => {
			// 	// 添加商品到购物车后的回调
			// 	_this.EVENTHUB.$emit('updateCount', data);
			// 	uni.showToast({'title': '已添加到购物车'});
			// }]);
		},
		clkRecommend (data) {
			turnPage('detail', data);
		},
		clkSpecs (index) {
			this.specsIndex = index;
		},
		// 选择规格
		chooseSpecs (index) {
			let specsInfo = this.goodInfo.specs[index];
			this.$set(this.goodInfo, 'unit', specsInfo.name);
			this.$set(this.goodInfo, 'price', specsInfo.price);
			this.$set(this.goodInfo, 'rprice', (this.goodInfo.rebate / 10 * specsInfo.price).toFixed(2));
		},
		showGg () {
			this.ggoption.show = true;
			this.$nextTick(function(){
				this.ggoption.mainShow = true;
			});
		},
		hideGg () {
			let _this = this;
			
			this.ggoption.mainShow = false;
			setTimeout(() => {
				_this.ggoption.show = false;
			}, 200);
		}
	}
}
